<template>
	<div id='ShopClass'>
		<!---->
			<div class="excavator container">
				<div class="excavator-filter ng-scope">
					<span class="excavator-filter-name">:</span>
					 <!---->
					<a class="excavator-filter-item ng-binding ng-scope" href="javascript:" v-for="(item,index) in shopClass"  @click="showShop(item,index,item.id)"  v-bind:class="{active:index==oneIndex}">
						<!--  -->
						{{item.name}}
					</a>
				  <!---->
					<div ng-show="subCategories" class="excavator-filter-subbox" v-show="shopDiv">
							<a class="excavator-filter-item ng-binding ng-scope" href="javascript:" v-for="(item,index) in shopClassList"  @click="changClass(item,index,item.id)" v-bind:class="{focus:index==twoIndex}">
							{{item.name}}
							</a>
					</div>
					<div class="excavator-filter-subbox ng-hide" v-show="shopDiv"></div>
				</div>
			</div>
			<!---->
	</div>
</template>
<script>
	export default{
		data(){
			return{
						//
						shopDiv:false,
						shopClass:[
							{
								name:'',
								id:1
							},
							{
								name:'',
								id:2,
								List:[
								{name:'',id:11},
								{name:'',id:12},
								{name:'',id:13},
								{name:'',id:14},
								{name:'',id:15},
								{name:'',id:16},
								{name:'',id:17},
								{name:'',id:18},
								{name:'',id:19}]},
							{
								name:'',
								id:3,
								List:[
									{name:'',id:11},
									{name:'',id:12},
									{name:'',id:13},
									{name:'',id:14},
									{name:'',id:15},
									{name:'',id:16},
									{name:'',id:17},
									{name:'',id:18},
									{name:'',id:19},
									{name:'',id:20},
									{name:'',id:21},
									{name:'',id:22},
									{name:'',id:23},
									{name:'',id:24}]},
							{
								name:'',
								id:4,
								List:[
									{name:'',id:11},
									{name:'',id:12},
									{name:'',id:13},
									{name:'',id:14},
									{name:'',id:15},
									{name:'',id:16},
									{name:'',id:17},
									{name:'',id:18},
									{name:'',id:19},
									{name:'',id:20}]},
								{
									name:'',
									id:5
								},
								{
									name:'',
									id:6},
								{
									name:'',
									id:7},
								{
									name:'',
									id:8},
								{
									name:'',
									id:9},
								{
									name:'',
									id:10},
								{
									name:'',
									id:11},
								{
									name:'',
									id:12},
								{
									name:'',
									id:13},
								{
									name:'',
									id:14},
								{
									name:'',
									id:15},
								{
									name:'',
									id:16
								}],
						shopClassList:[],//
						oneIndex:0,//
						twoIndex:0,//
						showList:[2,11],//
						temp:null//
			}
			
		},
		created(){
				this.$emit("showId",this.showList)//
		},
		methods:{
					//
			  	showShop(item,index,id){
			  		this.shopDiv=true
			  		this.shopClassList=item.List
			  		this.oneIndex=index
			  		this.twoIndex=0
			  		this.showList=[id,11]
			  		this.temp=id
			  		this.$emit("showId",this.showList)
			  	},
			  	//class
			  	changClass(item,index,id){
			  		this.twoIndex=index
			  		this.showList=[this.temp,id]
			  		this.$emit("showId",this.showList)
			  	}
		}

	}
</script>


