/**
 * vue/
 * 
 * 
 * 
 */
export default {
  install(Vue, options) {

    // 'Vue.prototype.xxx'

    //
    Vue.prototype.scrollTop = function(el, from = 0, to, duration = 500) {
      if (!window.requestAnimationFrame) {
        window.requestAnimationFrame = (
          window.webkitRequestAnimationFrame ||
          window.mozRequestAnimationFrame ||
          window.msRequestAnimationFrame ||
          function(callback) {
            return window.setTimeout(callback, 1000 / 60);
          }
        );
      }
      const difference = Math.abs(from - to);
      const step = Math.ceil(difference / duration * 50);

      function scroll(start, end, step) {
        if (start === end) return;

        let d = (start + step > end) ? end : start + step;
        if (start > end) {
          d = (start - step < end) ? end : start - step;
        }
        if (el === window) {
          window.scrollTo(d, d);
        } else {
          el.scrollTop = d;
        }
        window.requestAnimationFrame(() => scroll(d, end, step));
      }
      scroll(from, to, step);
    }

    //
    Vue.prototype.formatPrice = function(s, n = 2) {
      /*
       * 
       * s
       * n
       * */
      n = n > 0 && n <= 20 ? n : 2;
      s = parseFloat((s + "").replace(/[^\d\.-]/g, "")).toFixed(n) + "";
      var l = s.split(".")[0].split("").reverse(),
        r = s.split(".")[1];
      var t = "";
      for (var i = 0; i < l.length; i++) {
        t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? "," : "");
      }
      return t.split("").reverse().join("") + "." + r;
    }

    /// 
    //myGlobalMethod
    Vue.myGlobalMethod = function() {
      //...
    }

    //
    //drag
    Vue.directive('drag', function(el) {
      el.onmousedown = function(e) {
        //div-div
        var divx = e.clientX - el.offsetLeft;
        var divy = e.clientY - el.offsetTop;
        //onmousedowndivdocument.onmousemove
        document.onmousemove = function(e) {
          //div-divx/divy
          var l = e.clientX - divx;
          var t = e.clientY - divy;
          el.style.left = l + 'px';
          el.style.top = t + 'px';
        }
        document.onmouseup = function(e) {
          document.onmousemove = null;
          document.onmouseup = null;
        }
      }
    })

    //
    //NumberDateYYYY-MM-DD HH-MM-SS
    Vue.filter('formatTime',function (value) {
    	Date.prototype.Format = function(fmt){
    		var o ={
    			'M+':this.getMonth()+1,//
    			'd+':this.getDate(),//
    			'h+':this.getHours(),//
    			'm+':this.getMinutes(),//,
    			's+':this.getSeconds(),//,
    			'q+':Math.floor((this.getMonth()+3)/3),//
    			'S':this.getMilliseconds()//
    		};

    		if (/(y+)/.test(fmt)) 
    			fmt = fmt.replace(RegExp.$1,(this.getFullYear()+'').substr(4-RegExp.$1.length));
    		for (var k in o)
    			if (new RegExp("("+k+")").test(fmt)) 
    				fmt = fmt.replace(RegExp.$1,(RegExp.$1.length == 1)?(o[k]):(('00'+o[k]).substr((''+o[k]).length)))
    			return fmt;

    	}
    	return new Date(value).Format('yyyy-MM-dd hh:mm:ss')
    })


  }
}



