/*
  actionstoredispatch
  
  commitmutationstype
 */
import * as types from './types.js';
import Router from '@/router/index'
export default {
  addProduct: ({ commit, state },product) => {
      //
      commit(types.ADDPRODUCT,product);
  },
   /* //
  removeProduct: ({ commit, state },product) => {
      commit(types.REMOVEPRODUCT,product);
  },*/
      //
  addPrice:({ commit, state},price) =>{
      commit(types.ADDPRICE,price);
  },
      //
  lessPrice:({ commit, state},price) =>{
      commit(types.ADDPRICE,price);
  },
     //
  addCount:({ commit, state},id) =>{
      commit(types.ADDCOUNT,id);
  },
     /*//
  lessCount:({ commit, state},id) =>{
      commit(types.ADDCOUNT,id);
  },*/
     //
  clearShopCar:({ commit, state}) =>{
      commit(types.CLEARSHOPCAR);
  },
     //product
  clearShopCar:({ commit, state}) =>{
      commit(types.CLEARSHOPCAR);
  },
  //
  loadDataFromLocal:({ commit, state}) =>{
    if (JSON.parse(localStorage.getItem("shopCar"))!=null) {
        state.shopCar=JSON.parse(localStorage.getItem("shopCar"))
    }
    if (JSON.parse(localStorage.getItem("totalPrice"))!=null) {
        state.totalPrice=localStorage.getItem("totalPrice")
    }
    if (JSON.parse(localStorage.getItem("totalCount"))!=null) {
        state.totalCount=localStorage.getItem("totalCount")
    }
     /* state.totalPrice=localStorage.getItem("totalPrice")
      state.totalCount=localStorage.getItem("totalCount")*/
  },
  //
  writeDataToLocal:({ commit, state}) =>{
      localStorage.setItem("shopCar", JSON.stringify(state.shopCar))
      localStorage.setItem("totalPrice", state.totalPrice)
      localStorage.setItem("totalCount", state.totalCount)
  }
}



